import urljoin from 'url-join'
import axios from 'axios'
// import { fetch } from 'whatwg-fetch'
// require('es6-promise').polyfill();
// require('isomorphic-fetch');

const generateSourceDefaultUrl = () => {
  // return 'https://blockdiag-api.saino.me/';
  return 'http://localhost:8000/';
}

const blockdiagRender = (diagType, code, generateSourceUrl) => {
  return (async () => {
    try {
      const apiUrl = urljoin(generateSourceUrl, 'api', 'v1', diagType);
      const response = await axios.post(
        apiUrl,
        JSON.stringify({
          "source": code
        }),
        {
          headers: {
            "Content-Type": "application/json"
          }
        }
      );
      const json = await response.data;
      return json.image;
    } catch (error) {
      var errorType;
      var errorMessage;
      if (error.response.data) {
        errorType = error.response.data.etype;
        errorMessage = error.response.data.error;
      } else {
        errorType = error.response.status;
        errorMessage = error.response.statusText;
      }
      return `<pre>${errorType}\n${errorMessage}\n${code}</pre>`
    }
  })().then(result => result);
}

const BlockdiagPlugin = (md, options) => {
  options = options || {};

  var generateSourceUrl = options.generateSourceUrl || generateSourceDefaultUrl();

  const temp = md.renderer.rules.fence.bind(md.renderer.rules)
  md.renderer.rules.fence = (async (tokens, idx, options, env, slf) => {
    const token = tokens[idx]
    const code = token.content.trim()
    if (token.info === 'blockdiag') {
      var result = (async () => {
        return await blockdiagRender(token.info, code, generateSourceUrl);
      })();
      return result;
    }
    return temp(tokens, idx, options, env, slf);
  });
  // console.log((async (tokens, idx, options, env, slf) => {
  //   return temp(tokens, idx, options, env, slf);
  // })());
  // md.renderer.rules.fence = (tokens, idx, options, env, slf) => {
  //   return temp(tokens, idx, options, env, slf);
  // };
}

export default BlockdiagPlugin